<template lang="html">
  <div class="">
    <default-head header=""></default-head>
    <div id="wrapper" class="special-company-box">
      <div class="re-form-box">
        <flm-form-input label="" v-model="specialInfo.name"
        data-vv-name="" v-validate="'required'" name="name"
        placeholder=""></flm-form-input>
      </div>
      <div class="re-form-box">
        <flm-form-input label="" v-model="specialInfo.certificate"
          data-vv-name="" v-validate="'required'" name="certificate"
         placeholder=", "></flm-form-input>
      </div>
      <div class="pic-box f-re-martop">
        <img-block title="()" @submit-pic="getImages" sign="trademarkImg"
          :image="specialInfo.trademarkImg" message=", "
        ></img-block>
      </div>
      <div class="pic-box">
        <img-block title="()" @submit-pic="getImages" sign="attorneyAuthImg"
          :image="specialInfo.attorneyAuthImg"  message=": "
        ></img-block>
      </div>
      <div class="pic-box1">
        <img-block title="" @submit-pic="getImages" sign="specialAuthImgArray" type="moreone"
          message=": , "
        ></img-block>
      </div>
      <add-imgs :imgArr="specialInfo.specialAuthImgArray" sign="specialAuthImgArray"
        data-vv-name="" v-validate="'required'" data-vv-value-path="imgArr"
        @submit-pic="getImages" @delete-image="deleteImage"></add-imgs>
      <div v-if="specialInfo.status === 2 && specialInfo.auditSpecRemark"
      class="recognize-remark">: {{specialInfo.auditSpecRemark}}</div>
    </div>
    <foot-btn @click.native="submitRecognize"></foot-btn>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import imgBlock from '@/components/recognize/imgBlock'
import addImgs from '@/components/recognize/addImgs'
import footBtn from '@/components/common/footBtn'
export default {
  components: {defaultHead, imgBlock, addImgs, footBtn},
  data () {
    return {
      specialInfo: {
        userId: 0,
        name: '',
        certificate: '',
        trademarkImg: '',
        attorneyAuthImg: '',
        specialAuthImgArray: [],
        specialAuthStatus: 0,
        auditSpecRemark: ''
      }
    }
  },
  computed: {
    userId () {
      return utils.userId()
    }
  },
  mounted () {
    this.initPage()
  },
  methods: {
    isLogin () {
      if (!utils.isLogin()) {
        this.$router.push('/login')
        return false
      }
      return true
    },
    initPage () {
      if (!this.isLogin()) return
      this.ajaxGetCompanyInfo({userId: this.userId})
    },
    deleteImage (index) {
      this.specialInfo.specialAuthImgArray.splice(index, 1)
    },
    submitRecognize () {
      // 
      if (this.specialInfo.specialAuthStatus === 3) {
        minutils.MessageBox({message: ', '})
        return
      }
      if (this.specialInfo.specialAuthStatus === 1) {
        minutils.MessageBox({message: ', '})
        return
      }
      this.$validator.validateAll().then(() => {
        this.ajaxSaveSpecailRecognize(this.specialInfo)
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    getImages (data, sign) {
      if (sign === 'specialAuthImgArray' && (this.specialInfo.specialAuthImgArray.length + data.length) > 3) {
        minutils.Toast('')
        return
      }
      data.forEach((item, index) => {
        this.ajaxUploadPic({fileName: item.fileName, fileBase64: item.base64Img}, sign)
      })
    },
    ajaxGetCompanyInfo (data) {
      minutils.IndicatorOpen('...')
      api.getCompanyInfo(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess && result.data) {
          Object.keys(this.specialInfo).forEach(k => {
            if (result.data[k]) {
              this.specialInfo[k] = result.data[k]
            }
          })
        }
      })
    },
    ajaxSaveSpecailRecognize (data) {
      api.companySpecialAuthenticity(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          minutils.MessageBox({message: ''})
        }
      })
    },
    ajaxUploadPic (data, sign) {
      api.uploadPic(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          if (sign === 'specialAuthImgArray') {
            ((data, self) => {
              self.specialInfo.specialAuthImgArray.push(result.data)
            })(result.data, this)
          } else {
            this.specialInfo[sign] = result.data
          }
        }
      })
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/recognize/index.less';
.special-company-box {
  padding-bottom: 1.5rem;
}
.pic-box1 {
  background: @whiteColor;
  padding: .3rem .22rem;
}
</style>



